<template>
  <div class="date-time-wrap clearfix">
    <div class="date-box">
      <div class="date-box-date-l date-box-date">{{dates.getDate()}}</div>
      <div class="date-box-date-r">
        <div class="date-box-date-mon">{{dates.getMonth() | formatMon}}</div>
        <div class="date-box-date-year">{{dates.getFullYear()}}</div>
      </div>
    </div>
    <div class="time-box">
      {{dates.getHours()>9?dates.getHours():'0'+dates.getHours()}}:
      {{dates.getMinutes()>9?dates.getMinutes():'0'+dates.getMinutes()}}:
      {{dates.getSeconds()>9?dates.getSeconds():'0'+dates.getSeconds()}} {{dates.getDay() | formatDay}}
    </div>
  </div>
</template>

<script>
import utils from '../../utils/utils'

export default {
  data () {
    return {
      dates: new Date()
    }
  },
  // vue.js{{  }}(|)
  // vuefilters
  filters: {
    formatMon: function (value) {
      return utils.numToMon(value)
    },
    formatDay: function (value) {
      return utils.numToDay(value)
    }
  },
  // $el 
  created: function () {
    // do nothing
  },
  // el
  mounted () {
    var self = this
    this.timer = setInterval(() => {
      self.dates = new Date() // date
    }, 1000)
  },
  // addEventListener
  beforeDestroy: function () {
    if (this.timer) {
      clearInterval(this.timer) // Vue
    }
  }
}
</script>

